import request from '@/utils/request';
import { useEffect, useState } from 'react';
import useTheme from './useTheme';
import { ConfigServerThemeI } from './typings/ConfigTheme';

/**
 * 仅在宿主环境有效
 */
export interface LoadThemeArgs {
  djcGatewayBaseUrl: string;
  projectKey?: string;
  themeKey?: string;
  cache?: boolean;
  ttl?: number;
}

export const loadTheme = async ({
  djcGatewayBaseUrl,
  projectKey,
  themeKey = 'default',
  cache = true,
  ttl = 60 * 30,
}: LoadThemeArgs) => {
  const portalKey = projectKey || window.top?.location?.host;

  try {
    const { data } = await request(`${djcGatewayBaseUrl}/djcsupport/v1/portal-infos/portal-theme`, {
      params: { portalKey, themeKey },
      cache,
      ttl,
    });

    const themeConfig = data && data.themeSettings ? JSON.parse(data.themeSettings) : {};

    return themeConfig;
  } catch {
    return {};
  }
};

export const useLoadTheme = ({
  djcGatewayBaseUrl,
  projectKey,
  themeKey,
  cache,
  ttl,
}: LoadThemeArgs) => {
  const [theme, setTheme] = useState<ConfigServerThemeI>();

  /**
   * 宿主环境从服务器端加载配置
   */
  useEffect(() => {
    if (window.top === window.self) {
      if (djcGatewayBaseUrl)
        loadTheme({ djcGatewayBaseUrl, projectKey, themeKey, cache, ttl }).then(setTheme);
      else {
        setTheme({});
      }
    }
  }, [cache, djcGatewayBaseUrl, projectKey, themeKey, ttl]);

  /**
   * 子应用从 redux 中获取配置
   */
  const parentTheme = useTheme();

  if (window.top !== window.self) {
    // 构造为和配置服务器返回相同的结构
    return { base: parentTheme };
  }

  return theme;
};
